import React from "react";
import "./NavBar.css";
import { Link } from "react-router-dom";
import Logo from "../images/HYM.png";

export default function NavBar() {
  return (
    <>
      <div className="container-fluid">
        <nav className="navbar navbar-expand-lg bg-body-tertiary" id="navId">
          <div className="container-fluid">
            <Link className="navbar-brand" id="logo" to="/home">
              <img src={Logo} alt="HYM"/>
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="/navbarNavDropdown"
              aria-controls="navbarNavDropdown"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse justify-content-end"
              id="navbarNavDropdown"
            >
              <ul className="navbar-nav">
                <li className="nav-item">
                  <Link className="nav-link" aria-current="page" to="/HYM">
                    Home
                  </Link>
                </li>
                <li class="nav-item dropdown">
                    <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                        Menu
                    </a>
                    <ul class="dropdown-menu">
                        <li><a class="dropdown-item" href="#">High Calorie</a></li>
                        <li><a class="dropdown-item" href="#">Low Calorie</a></li>
                        <li><a class="dropdown-item" href="#">Ketos</a></li>
                        <li><a class="dropdown-item" href="#">Fruit Oat Meal Bowl</a></li>
                    </ul>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/about">
                    About Us
                  </Link>
                </li>
                
                <li className="nav-item">
                  <Link className="nav-link" to="/cart">
                    <span class="material-symbols-outlined">shopping_cart</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </>
  );
}
