import React from "react";
import Banner from "../images/Eat Healthy Thanks us later.png";
import "./homepage.css";
import ProductCard from "../components/productcard";
import products from '../components/product';

function Homepage() {
    return(
        <div>
            <div className="container">
                <img src={Banner} alt="HYM" className="banner"></img>
            </div>

            <hr/>

            <p className="protein">Protein Meal</p>

            <div className="container">
                    <div className="product">



                        <div className="row">
                            {products.map((product, index) => (
                                <div className="col-md-4" >
                                    <ProductCard productName={product.productName} price={product.price} imageUrl={product.imageUrl} item={product} key={index}/>       
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
        </div>
    );
}

export default Homepage;